html {
  font-size: 62.5%; /* 16px x 62.5 = 10px = 1rem */
  box-sizing: border-box;
}

body {
  margin: 0;
  height: 100vh;
  font-size: 1.6rem;
  font-family: Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  height: 100%;
}

:root {
  --tooltip-bg: #02c2a5de;
  --dark: #000;
  --light: #fff;
}

/* Layout con GRID */
.grid-container {
  display: grid;
  grid-template-areas:
    'header'
    'main'
    'footer';
  grid-template-columns: 1fr;
  grid-template-rows: 6rem 1fr 3rem;
  height: 100%;
}
header {
  grid-area: header;
  background-color: #203040;
  z-index: 99;
  height: 60px;
}
main {
  grid-area: main;
}
footer {
  grid-area: footer;
  background-color: #203040;
  color: #e0dbdb;
}

/*NavBar Header (barra de menus) */

header a {
  color: #ffffff;
  padding: 0 1.2rem;
}

a:hover,
.fa-cart-arrow-down:hover,
.fa-user:hover,
.fa-user-shield:hover,
.fa-user-gear:hover {
  color: rgb(8, 146, 146);
}

.logo {
  height: 50px;
  width: 50px;
  border-radius: 50%;
  margin-left: 15px;
  pointer-events: all;
  cursor: pointer;
}

a.brand {
  color: #fff;
  font-size: 2rem;
  font-weight: bold;
}

.badge {
  position: relative;
  background-color: #f02020;
  color: #ffffff;
  border-radius: 50%;
  padding: 0.2rem 0.7rem;
  font-size: 1.4rem;
  left: 0.5rem;
  bottom: 0.5rem;
}

/* Dropdown */

.link-usuario {
  position: relative;
  top: -5px;
  left: -10px;
}

.fa-caret-down {
  font-size: 36px;
  position: relative;
  top: 6px;
  left: 8px;
}

.dropdown {
  display: inline-block;
  position: relative;
}

.dropdown-content {
  position: absolute;
  display: none;
  min-width: 13rem;
  z-index: 1;
  background-color: #4db6ac;
  margin: 0;
  padding-bottom: 10px;
  border-radius: 0.5rem;
}
.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown-content li a:hover {
  color: #000;
}

.hidden {
  display: none;
}

/* COMMONS */
h1 {
  font-size: 3rem;
  color: #203040;
  margin: 0 1rem 1rem 0;
}
h2 {
  font-size: 2rem;
  color: #203040;
  margin: 0;
}
h3 {
  font-size: 1.2rem;
  margin: 0;
}

h3.nombre-prod {
  color: white;
}

h1.clean,
h2.clean {
  margin: 10px 5px 5px 5px;
}

a {
  text-decoration: none;
  color: black;
  font-weight: bold;
}

a h2:hover {
  color: blue;
  font-weight: bold;
}

.carrito-ul {
  min-width: 600px;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

ul h1 {
  margin: 10px 0;
}

li {
  margin-top: 0.7rem;
}

li p {
  font-size: 1.5rem;
  font-weight: bold;
  margin: 5px 0;
  display: inline-block;
  padding-right: 10px;
  color: #293d52;
  line-height: 15px;
}

.ficha-tecnica {
  font-size: 1.3rem;
}

li p span {
  display: inline-block;
}

input,
textarea,
button {
  outline: none;
  padding: 0.6rem 1rem;
  border-radius: 0.5rem;
  border: 0.1rem #afb4b3 solid;
  font-size: 1.6rem;
  font-family: Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

button {
  cursor: pointer;
  font-weight: bold;
}

input[type='radio'] {
  margin: 5px;
}

button.primary {
  margin-top: 25px;
  background-color: #2020a0;
  color: #fff;
}

button.admin {
  margin: 5px 0;
  background-color: #2020a0;
  width: 100%;
  color: #fff;
}

button.block {
