/* Tool-tip component styles */

.tooltip {
  position: absolute;
  padding: 10px;
  background-color: var(--tooltip-bg);
  color: var(--light);
  min-width: 100px;
  border-radius: 5px;
  opacity: 0;
  transition-property: opacity, transform;
  transform-origin: center center;
  text-align: center;
  z-index: 999;
}

.tooltip--top {
  transform: translate(-50%, -15px) scale(0);
}
.tooltip--bottom {
  transform: translate(-50%, 15px) scale(0);
}
.tooltip--left {
  transform: translate(-15px, -50%) scale(0);
}
.tooltip--right {
  transform: translate(15px, -50%) scale(0);
}
.tooltip--top-left {
  transform: translate(0, -15px) scale(0);
}
.tooltip--top-right {
  transform: translate(-100%, -15px) scale(0);
}
.tooltip--bottom-left {
  transform: translate(0, 15px) scale(0);
}
.tooltip--bottom-right {
  transform: translate(-100%, 15px) scale(0);
}
.tooltip--left-top {
  transform: translate(-15px, 0) scale(0);
}
.tooltip--left-bottom {
  transform: translate(-15px, -100%) scale(0);
}
.tooltip--right-top {
  transform: translate(15px, 0) scale(0);
}
.tooltip--right-bottom {
  transform: translate(15px, -100%) scale(0);
}

.tooltip::after {
  border: solid transparent;
  content: '';
  height: 0;
  width: 0;
  position: absolute;
  border-color: rgba(255, 0, 0, 0);
  border-width: 6px;
}

.tooltip--top::after {
  top: 100%;
  left: 50%;
  border-top-color: var(--tooltip-bg);
  margin-left: -6px;
}
.tooltip--bottom::after {
  bottom: 100%;
  left: 50%;
  border-bottom-color: var(--tooltip-bg);
  margin-left: -6px;
}
.tooltip--left::after {
  top: 50%;
  left: 100%;
  border-left-color: var(--tooltip-bg);
  margin-top: -6px;
}
.tooltip--right::after {
  top: 50%;
  right: 100%;
  border-right-color: var(--tooltip-bg);
  margin-top: -6px;
}
.tooltip--top-left::after {
  top: 100%;
  left: 0%;
  border-top-color: var(--tooltip-bg);
  margin-left: 10px;
}
.tooltip--top-right::after {
  top: 100%;
  right: 0%;
  border-top-color: var(--tooltip-bg);
  margin-right: 10px;
}
.tooltip--bottom-left::after {
  bottom: 100%;
  left: 0%;
  border-bottom-color: var(--tooltip-bg);
  margin-left: 10px;
}
.tooltip--bottom-right::after {
  bottom: 100%;
  right: 0%;
  border-bottom-color: var(--tooltip-bg);
  margin-right: 10px;
}
.tooltip--left-top::after {
  top: 10px;
  left: 100%;
  border-left-color: var(--tooltip-bg);
}
.tooltip--left-bottom::after {
  bottom: 10px;
  left: 100%;
  border-left-color: var(--tooltip-bg);
}
.tooltip--right-top::after {
  top: 10px;
  right: 100%;
  border-right-color: var(--tooltip-bg);
}
.tooltip--right-bottom::after {
  bottom: 10px;
  right: 100%;
  border-right-color: var(--tooltip-bg);
}

/* CUSTOM INPUTS */
.input__div {
