  margin: 10px 0px;
  position: relative;
}

.input__div input {
  height: 100%;
  width: 360px;
  outline: none;
  font-size: 1.3rem;
  transition: all 0.3s ease;
  padding: 0.9rem 1rem 0.2rem 1rem;
  border-radius: 0.5rem;
  border: 0.1rem #afb4b3 solid;
  font-family: Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: rgb(88, 87, 87);
}

.input__div input:focus {
  border-color: #2b7068;
}

.input__div label {
  position: absolute;
  top: 50%;
  left: 15px;
  color: #807f7f;
  font-weight: 400;
  font-size: 12px;
  pointer-events: none;
  transform: translateY(-50%);
  transition: all 0.3s ease;
}

.input__div input:focus ~ label,
.input__div input:valid ~ label {
  top: 0%;
  font-size: 12px;
  font-weight: bold;
  color: rgb(5, 78, 78);
  background: #fff;
  transform: translateY(-50%);
}

